.page-header {
  position: relative;
  width: 320px;
  margin: 0 auto;
}

.page-header__logo {
  padding-top: 11px;
  padding-bottom: 13px;
  padding-left: 20px;

  border-bottom: 1px solid #e6e6e6;
}

.page-header__logo a[href] {
  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

@media (min-width: $tablet-width) {
  .page-header {
    display: flex;
    justify-content: space-between;
    width: 708px;
    padding: 0 30px;
  }

  .page-header__logo {
    margin-top: 25px;
    margin-bottom: 20px;
    padding: 0;

    border: none;
  }

  .page-header__logo img {
    width: 173px;
    height: 58px;
  }
}

@media (min-width: $desktop-width) {
  .page-header {
    width: 1220px;
    padding: 0 40px;
  }

  .page-header__logo img {
    width: 202px;
    height: 59px;
    margin-top: 30px;
    margin-bottom: 32px;
  }

  .page-header--index {
    .site-list__item a {
      color: $white;
    }

    .site-list__item--active {
      border-color: $white;
    }
  }
}
